
import {
	login
} from './utils/login.js'
export default {
	globalData: {

	},
	onLaunch: function() {
		console.log('App Launch')
		this.$state.setState({
			userAuth: false
		})
		login()
			.then(r => {
				this.$state.userAuth = r;
			})
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	}
}
